.form {
  padding: 244px 0 306px;
  background-image: url(../img/wzzbg.svg);
  background-repeat: no-repeat;
  background-position: center 633px;
  background-size: 100%;

  @media (max-width: 1050px){
    background-position: center 668px;
  }

  @media (max-width: 768px){
    padding-top: 198px;
    padding-bottom: 162px;
    background-position: center 740px;
  }
  @media (max-width: 430px){
    background-position: center 826px;
  }

  &__title {
    margin-bottom: 6px;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      width: 150px;
      height: 150px;
      background-image: url('../img/alarm.png');
      background-repeat: no-repeat;
      left: 50%;
      transform: translateX(-50%);
      bottom: 92px;
    }
  }

  &__text {
    @include text-sm();
    margin-bottom: 56px;
    margin-top: 6px;
    position: relative;

    &::after {
      position: absolute;
      content: '';
      width: 238px;
      height: 161px;
      background-image: url(../img/bg-form.svg);
      background-size: cover;
      left: -288px;
      bottom: 50px;
    }

  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__input,
  &__textarea {
    font-size: $fourteenPx;
    color: $mainBlack;
    outline: none;
    resize: none;
    padding: 15px 30px 15px 0;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    &::placeholder {
      font-size: $fourteenPx;
      color: gray;
    }

    &:focus,
    &:active {
      &:first-child {
      }
      border-top: none;
    }
  }

  &__textarea {
    padding-bottom: 21px;
  }

  &__submit-button {
    transition: background-color 0.5s ease-in;
    outline: none;
    display: flex;
    align-items: center;
    margin: 70px auto 0;
    cursor: pointer;
    @include main-link($color: $mainWhite, $bgc: $dodgerBlue, $padding: 11px 68px);

    &:hover,
    &:focus {
      background-color: #3f82d5;
      border-color: #3f82d5;
    }

    &:disabled {
      cursor: unset;
    }

    &.success {
      padding: 11px 64px;
      background-image: url("../img/check.svg");
      background-repeat: no-repeat;
      background-size: contain;
      background-color: #44df75;
      border-color: transparent;
    }

    &.--show {
      font-size: 0;
      padding: 6.5px 81px;
      .spinner {
        display: block;
      }
    }
  }
}

.spinner {
  display: none;
  width: 1.7rem;
  height: 1.7rem;
  border-radius: 50%;
  border: 0.3rem solid rgba($mainWhite, 0.3);
  border-top-color: $mainWhite;
  animation: 1.5s spin infinite linear;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
