.navigation {
  display: flex;
  align-items: center;

  &__logo {
    flex-shrink: 0;
    }

    img {
      --ease: all 400ms ease-in-out;
      transition: var(--ease);
    }

  &__text {
    font-size: $fifteenPx;

    @media (max-width: 620px) {
      display: none;
    }
  }

  &__form-link {
    margin-left: auto;
    @include main-link($color: $dodgerBlue);

    &:hover {
      color: $mainWhite;
      background-color: $dodgerBlue;
    }
  }
}

