.tech-stack {
  padding-bottom: 95px;

  @media (max-width: 768px){
    padding-bottom: 55px
  }

  &__list {
    &.container {
      width: 100%;
    }

    max-width: 1280px;

    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(73px,1fr));
    grid-auto-rows: auto;
    gap: 0 59px;
    margin-top: 35px;

    padding: 0;
    list-style-type: none;

    @media (max-width: 1400px){
      grid-template-columns: repeat(8,minmax(42px,1fr));
    }

    @media (max-width: 960px){
      grid-template-columns: repeat(4,minmax(42px,1fr));
    }

    @media (max-width: 768px){
      margin-top: 0;
      grid-template-columns: repeat(8,minmax(20px,1fr));
    }

    @media (max-width: 695px){
      grid-template-columns: repeat(4,minmax(20px,1fr));
    }
  }

  &__img-wrapper {
    display: flex;
    height: 90px;
    max-width: 90px;
    margin: 0 auto;
    img {
      margin: auto;
    }
  }

  &__item {
    margin-top: 30px;
  }

  &__card-title {
    @include text-l();
    margin-top: 30px;
    text-align: center;

    @media (max-width: 768px){
      margin-top: 0;
    }

    @media (max-width: 695px){
      margin-top: 25px;
    }

    @media (max-width: 468px){
      margin-top: 0;
    }
  }

  &__description {
    @include text-sm(30px);
    margin-top: 70px;

    &.--last-child {
      margin-top: 36px;
      margin-bottom: -15px;
    }

    @media (max-width: 375px) {
      text-align: left;
    }
  }
}
