html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  letter-spacing: 0;
  font-weight: 400;
}

img {
  max-width: 100%;
  height: auto;
}
