.footer {
  background-color: $mainBlack;
  padding: 35px 15px 40px;
  color: $mainWhite;
  display: flex;
  flex-direction: column;

  &__title {
    font-size: 12px;
    line-height: 15px;
    font-weight: 600;
  }

  &__content {
    width: fit-content;
    margin: auto;
    text-align: center;
  }

  &__contacts {
    @include text-l();
    margin: 25px 0;
  }

  &__copyright {
    @include text-l($font-weight: 400);
  }
}
