.main-promo {
  padding: 144px 0 283px;
  transform: translate(0, -110px);
  background-image: url('../img/hr-manager.png'), url('../img/main-bg.svg');
  background-repeat: no-repeat;
  background-position: 80% center, bottom;

  @media (max-width: 1050px) {
    background-position: 104% center, bottom;
  }
  @media (max-width: 890px) {
    background-position: 104% 386px, bottom;
    padding-bottom: 471px;
  }

  @media (max-width: 768px) {
    background-image: none;
    padding-bottom: 0;
  }

  @media (max-width: 580px) {
    background-size: 60% 50%,contain;
    background-position: center 358px,100% 460px;
  }

  @media (max-width: 460px) {
    background-size: 80% 50%,contain;
    background-position: center 358px,100% 500px;
  }
  @media (max-width: 460px) {
    background-position: center 393px,100% 500px;
  }

  @media (max-width: 379px) {
    background-size: 100% 50%,contain;
    background-position: center 439px,100% 602px;
  }

  &__img {
    display: none;

    @media (max-width: 768px) {
      display: flex;
    }
  }

  &__bg-tablet {
    @media (max-width: 768px) {
      z-index: -1;
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__hr-manager-img {
    margin: auto;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    max-width: 750px;
    font-size: 45px;
    line-height: 55px;
    letter-spacing: 2px;
    font-weight: 600;
    color: $abbey;
    font-family: $base-font-family;
    text-align: left;

    padding-top: 100px;
    margin-top: -100px;

    @media (max-width: 750px) {
      font-size: 32px;
      line-height: 40px;
      letter-spacing: 1px;
    }
  }

  &__slogan {
    font-size: 45px;
    font-weight: 600;
    color: $dodgerBlue;
    font-family: $base-font-family;

    @media (max-width: 750px) {
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__description {
    @include text-sm($text-align: left);
    margin-top: 34px;
    max-width: 410px;
  }
}
