.story {
  padding-top: 305px;
  padding-bottom: 40px;

  background-image: url('../img/middle-bg.svg');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 100% 176%;

  @media (max-width: 768px){
    padding-top: 230px;
    background-image: unset;
  }

  .container {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 71px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 109px;
      height: 94px;
      top: -113px;
      left: 50%;
      transform: translateX(-50%);
      background-repeat: no-repeat;
      background-image: url("../img/skewed.svg");
    }
  }

  &__main-description {
    font-size: 22px;
    line-height: 32px;
    text-align: left;
    max-width: 430px;
    margin: 0;

    @media (max-width: 768px){
      max-width: unset;
      text-align: center;
      margin-bottom: 43px;
    }

    @media (max-width: 750px){
      font-size: 20px;
      line-height: 29px;
    }

    @media (max-width: 375px) {
      text-align: left;
    }
  }

  &__description {
    @include text-sm($text-align: left);
    padding-bottom: 20px;
    margin: 0;

    @media (max-width: 768px){
      text-align: center;
    }

    @media (max-width: 375px) {
      text-align: left;
    }
  }

  &__content-wrapper {
    display: flex;

    @media (max-width: 768px){
      flex-direction: column;
    }
  }

  &__list {
    @include reset-list();
    max-width: 513px;
    margin-left: auto;

    @media (max-width: 768px){
      max-width: unset;
    }
  }

  &__item {
    margin-bottom: 49px;

    @media (max-width: 768px){
      margin-bottom: 31px;
    }

    @media (max-width: 700px){
      font-size: 20px;
      margin-bottom: 11px;
    }
  }
}
