@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin text-sm ($line-height: 20px, $text-align: center) {
  font-size: 14px;
  line-height: $line-height;
  color: $abbey;
  font-weight: 400;
  text-align: $text-align;
}

@mixin text-l ($font-weight: 600) {
  font-size: 12px;
  line-height: 15px;
  font-weight: $font-weight;
}


@mixin main-link ($color: $dodgerBlue, $bgc: $mainWhite, $padding: 7px 38px) {
  color: $color;
  font-family: "Roboto", "Arial", sans-serif;

  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  padding: $padding;
  border-radius: 30px;
  border: 1px solid $dodgerBlue;
  background-color: $bgc;
  flex-shrink: 0;
}
