.header {
  padding: 25px 0;
  position: sticky;
  top: 0;
  background-color: transparent;
  z-index: 9;
  --ease: all 400ms ease-in-out;
  transition: var(--ease);

  &.fade-in {
    background-color: rgba(0,0,0,.8);
    margin-bottom: 30px;
    padding: 11px 0;

    .navigation__text {
      color: $mainWhite;
    }

    .navigation__form-link  {
      color: $mainWhite;
      border-color: $dodgerBlue;
      background-color: $dodgerBlue;
    }

    .navigation__form-link:hover {
      background-color: rgba($dodgerBlue, 0.8);
      border-color: #3f82d5;
    }
  }
}
